"use client";

import { ColumnDef } from "@tanstack/react-table";
import { MoreHorizontal, ArrowUpDown } from "lucide-react";
import { Button } from "@/components/ui/button";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { Log } from "@/data/mock";

export const columns: ColumnDef<Log>[] = [
  {
    accessorKey: "clientName",
    header: ({ column }) => {
      return (
        <Button
          variant="ghost"
          onClick={() => column.toggleSorting(column.getIsSorted() === "asc")}
        >
          Client
          <ArrowUpDown className="ml-2 h-4 w-4" />
        </Button>
      );
    },
    cell: ({ row }) => (
        <div className="font-medium">{row.getValue("clientName")}</div>
    ),
  },
  {
    accessorKey: "communicationDate",
    header: "Date",
    cell: ({ row }) => new Date(row.getValue("communicationDate")).toLocaleDateString(),
  },
  {
    accessorKey: "media",
    header: "Media",
  },
  {
    accessorKey: "notes",
    header: "Notes",
    cell: ({ row }) => <div className="truncate max-w-xs">{row.getValue("notes")}</div>,
  },
  {
    id: "actions",
    cell: ({ row }) => {
      const log = row.original;
      return (
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="ghost" className="h-8 w-8 p-0">
              <span className="sr-only">Open menu</span>
              <MoreHorizontal className="h-4 w-4" />
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end">
            <DropdownMenuLabel>Actions</DropdownMenuLabel>
            <DropdownMenuItem>View Full Note</DropdownMenuItem>
            <DropdownMenuItem>Edit Log</DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      );
    },
  },
];
